import React from 'react';
import HeaderSection from 'components/organism/HeaderSection/HeaderSection';
import { HeaderParagraph } from 'components/atoms/HeaderParagraph/HeaderParagraph';
import styled from 'styled-components';

const Wrapper = styled.div`
  width: 128rem;
  padding-bottom: 3rem;
  @media (max-width: 1280px) {
    max-width: 102.4rem;
  }

  @media (max-width: 1024px) {
    max-width: 100%;
    padding-bottom: 3rem;
  }

  h1 {
    text-align: left;
  }

  ${HeaderParagraph} {
    @media (max-width: 800px) {
      padding: 0;
    }
  }
`;
const AboutPage = () => {
  return (
    <Wrapper>
      <HeaderSection title={'Cze, nazywam si Kasia Domagaa'} isButton={'Sprawd'} marginTop={'7rem'} pathButton={'/offer'}>
        <HeaderParagraph>
          Pracuj jako Digital Marketing Manager. Posiadam wieloletnie dowiadczenie w prowadzeniu kampanie e-marketingowych. Pracowaam zarwno w
          agencjach reklamowych, jak i bezporednio po stronie klienta. Centrum moich zainteresowa s dziaania reklamowe na Facebooku i Instagramie
          i wycznie na nich obecnie si skupiam. Planuje i optymalizuje kampanie, ktre realizuj konkretne cele biznesowe.
        </HeaderParagraph>
        <HeaderParagraph>Sprawd, co mog zrobi wanie dla Ciebie.</HeaderParagraph>
      </HeaderSection>
    </Wrapper>
  );
};

export default AboutPage;
