import React from 'react';
import styled from 'styled-components';
import { Paragraph } from 'components/atoms/Paragraph/Paragraph';
import { SecondTitle } from 'components/atoms/SecondTitle/SecondTitle';
import BenefitCards from 'components/molecues/BenefitCards/BenefitCards';
import { Button } from 'components/atoms/Button/Button';
import { Link } from 'react-router-dom';
import { resetScroll } from '../../../utilities/resetScroll';

const Wrapper = styled.div`
  padding: 10.4rem 0;

  @media (max-width: 800px) {
    padding: 5rem;
  }
`;

const BenefitSection = () => {
  return (
    <Wrapper>
      <SecondTitle>Co zyskasz prowadzc kampani reklamow na facebooku i instagramie?</SecondTitle>
      <Paragraph>W zalenoci od celw biznesowych Twojej marki korzyci prowadzenie dziaa reklamowych w social mediach s nastpujce:</Paragraph>
      <BenefitCards />
      <Link to="free-pricing" onClick={resetScroll}>
        <Button isPrimary>Darmowa Wycena</Button>
      </Link>
    </Wrapper>
  );
};

export default BenefitSection;
