import React from 'react';
import MainHeaderSection from 'components/organism/MainHeaderSection/MainHeaderSection';
import ViewWrapper from 'components/molecues/ViewWrapper/ViewWrapper';
import { SecondTitle, SecondTitleLowerCase } from 'components/atoms/SecondTitle/SecondTitle';
import BenefitSection from 'components/organism/BenefitSection/BenefitSection';
import PriceCardSection from 'components/organism/PriceCardSection/PriceCardSection';
import ContactForm from 'components/organism/ContactForm/ContactForm';
import Section from 'components/organism/Section/Section';

const HomePage = () => {
  return (
    <>
      <ViewWrapper color={'white'}>
        <MainHeaderSection />
      </ViewWrapper>
      <ViewWrapper color={'lightGrey'}>
        <Section title={'Kiedy warto zdecydowa si na\nkampanie reklamow na facebooku?'} isButton={'Dowiedz si wicej'} path={'/offer'}>
          Dziaania reklamowe w social mediach s w stanie przynie korzyci kadej firmy na wielu rnych paszczyznach. Sprawdz si zarwno w
          sytuacjach wprowadzenia nowego produktu na rynek przez renomowane marki, jak rwnie w przypadku budowania swojej obecnoci w sieci przez
          nowo-powstae marki. Kady moment rozwoju marki jest odpowiedni, aby rozpocz kampani reklamow na Facebooku i Instagramie.
        </Section>
        <BenefitSection />
        <SecondTitle>Cennik kampani reklamowych</SecondTitle>
        <PriceCardSection />
      </ViewWrapper>
      <ViewWrapper color={'white'}>
        <SecondTitleLowerCase>
          Nie wiesz, ktry pakiet wybra, masz dodatkowe pytania
          <br /> lub chcesz podj wspprac? Skontaktuj si ze mn.
        </SecondTitleLowerCase>
        <ContactForm />
      </ViewWrapper>
    </>
  );
};

export default HomePage;
